<script setup lang="ts">
import { defineProps } from "vue";
import { IllustratedStoryType } from "@/types/Illustration";
import router from "@/router";

interface Props {
  illustration: IllustratedStoryType;
}
defineProps<Props>();

const exploreIllu = (illustration: IllustratedStoryType) => {
  if (illustration.type === "group") {
    router.push(`/group/${illustration.id}`);
  } else if (illustration.type === "illustration") {
    router.push(`/illustration/${illustration.id}`);
  }
};
</script>

<template>
  <div @click="exploreIllu(illustration)">
    <img
      class="illustration"
      :src="require(`@/assets/${illustration.imageUrl}`)"
      :alt="illustration.title"
    />
  </div>
</template>

<style scoped>
.illustration {
  width: 100%;
  max-width: 300px;
  cursor: pointer;
}

@media (min-width: 768px) {
  .illustration {
    max-width: 400px;
  }
}

@media (min-width: 1024px) {
  .illustration {
    max-width: 600px;
  }
}
</style>
