<script setup lang="ts">
import { onMounted, ref } from "vue";
import { fetchIllustrations } from "@/illustrationService";
import { IllustratedStoryType } from "@/types/Illustration";
import IllustrationsGroup from "@/components/IllustrationsGroup.vue";

const loading = ref(true);

let illustrations = [] as IllustratedStoryType[];

onMounted(async () => {
  try {
    console.log("Fetching illustrations...");
    illustrations = await fetchIllustrations();
    console.log("Illustrations", illustrations[0].imageUrl);
  } catch (error) {
    console.error("Error fetching illustrations", error);
  } finally {
    loading.value = false;
  }
});
</script>

<template>
  <div>
    <h1>Illustrations Explorer</h1>
    <div v-if="loading">Loading illustrations...</div>
    <div v-else>
      <illustrations-group :illustrations="illustrations" />
    </div>
  </div>
</template>

<style scoped>
h1 {
  margin-bottom: 0;
  color: #393939;
}

.illustration {
  width: 100%;
  max-width: 300px;
  cursor: pointer;
}

@media (min-width: 768px) {
  .illustration {
    max-width: 400px;
  }
}

@media (min-width: 1024px) {
  .illustration {
    max-width: 600px;
  }
}
</style>
